import React, { useState } from 'react';
import { supabase } from './supabaseClient';

interface SignupPageProps {
  onSignupSuccess: () => void;
  onBackToLogin: () => void;
}

const SignupPage: React.FC<SignupPageProps> = ({ onSignupSuccess, onBackToLogin }) => {
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    confirmPassword: '',
    fullName: '',
    company: '',
    phone: ''
  });
  const [agreeToTerms, setAgreeToTerms] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');
  const [successMessage, setSuccessMessage] = useState('');
  const [canResendEmail, setCanResendEmail] = useState(false);
  const [resendCooldown, setResendCooldown] = useState(0);
  const [lastSignupData, setLastSignupData] = useState<any>(null);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
    setError('');
  };

  const validateForm = () => {
    if (!formData.email || !formData.password || !formData.confirmPassword || !formData.fullName) {
      setError('   .');
      return false;
    }

    if (!agreeToTerms) {
      setError('  .');
      return false;
    }

    //   
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(formData.email)) {
      setError('   .');
      return false;
    }

    //  
    if (formData.password.length < 6) {
      setError('  6  .');
      return false;
    }

    if (formData.password !== formData.confirmPassword) {
      setError('  .');
      return false;
    }

    return true;
  };

  const handleResendEmail = async () => {
    if (!lastSignupData) return;
    
    setIsLoading(true);
    setError('');
    
    try {
      const { error: resendError } = await supabase.auth.resend({
        type: 'signup',
        email: lastSignupData.email,
        options: lastSignupData.options
      });
      
      if (resendError) {
        setError('    : ' + resendError.message);
      } else {
        setSuccessMessage('   !  .');
        setCanResendEmail(false);
        setResendCooldown(60);
        
        const timer = setInterval(() => {
          setResendCooldown(prev => {
            if (prev <= 1) {
              clearInterval(timer);
              setCanResendEmail(true);
              return 0;
            }
            return prev - 1;
          });
        }, 1000);
      }
    } catch (error) {
      console.error('  :', error);
      setError('    .');
    } finally {
      setIsLoading(false);
    }
  };

  const handleSignup = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setIsLoading(true);
    setError('');

    try {
      // Supabase Auth 
      const { data, error: signUpError } = await supabase.auth.signUp({
        email: formData.email,
        password: formData.password,
        phone: formData.phone, //  phone  
        options: {
          data: {
            full_name: formData.fullName,
            company: formData.company,
            phone: formData.phone,
            role: 'user' //   user
          },
          emailRedirectTo: process.env.REACT_APP_EMAIL_REDIRECT_URL || `${window.location.origin}/auth/callback`
        }
      });

      if (signUpError) {
        if (signUpError.message.includes('already registered')) {
          setError('  .');
        } else {
          setError(signUpError.message);
        }
        return;
      }

      if (data?.user) {
        setSuccessMessage(' !    .');
        setLastSignupData({
          email: formData.email,
          password: formData.password,
          options: {
            data: {
              full_name: formData.fullName,
              company: formData.company,
              phone: formData.phone,
              role: 'user'
            },
            emailRedirectTo: process.env.REACT_APP_EMAIL_REDIRECT_URL || `${window.location.origin}/auth/callback`
          }
        });
        
        // 60   
        setResendCooldown(60);
        const timer = setInterval(() => {
          setResendCooldown(prev => {
            if (prev <= 1) {
              clearInterval(timer);
              setCanResendEmail(true);
              return 0;
            }
            return prev - 1;
          });
        }, 1000);
      }
    } catch (error) {
      console.error(' :', error);
      setError('   .');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
      <div className="bg-white p-8 rounded-lg shadow-lg w-full max-w-md">
        <div className="text-center mb-8">
          <div className="flex items-center justify-center gap-2 mb-3">
            <img src="/wideincheon-logo.png" alt="" className="h-8 w-auto" />
            <span className="text-gray-400 text-lg"></span>
            <img src="/youngjin-logo.png" alt="" className="h-8 w-auto" />
          </div>
          <h1 className="text-3xl font-bold text-gray-800"></h1>
          <p className="text-gray-600 mt-2">    </p>
        </div>

        {successMessage ? (
          <div className="space-y-4">
            <div className="bg-green-50 border border-green-200 text-green-800 px-4 py-3 rounded-md">
              <p className="font-semibold mb-2">{successMessage}</p>
              <p className="text-sm">
                     .<br />
                    .
              </p>
            </div>
            
            <div className="bg-blue-50 border border-blue-200 text-blue-800 px-4 py-3 rounded-md">
              <p className="text-sm font-semibold mb-1">   ?</p>
              <ul className="text-sm list-disc list-inside space-y-1">
                <li>  </li>
                <li>   </li>
                <li>    </li>
              </ul>
            </div>
            
            <div className="space-y-3">
              {canResendEmail ? (
                <button
                  onClick={handleResendEmail}
                  className="w-full bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 transition-colors"
                >
                    
                </button>
              ) : resendCooldown > 0 ? (
                <button
                  disabled
                  className="w-full bg-gray-400 text-white py-2 px-4 rounded-md cursor-not-allowed"
                >
                    {resendCooldown}
                </button>
              ) : null}
              
              <button
                onClick={onBackToLogin}
                className="w-full bg-gray-600 text-white py-2 px-4 rounded-md hover:bg-gray-700 transition-colors"
              >
                  
              </button>
            </div>
          </div>
        ) : (
          <form onSubmit={handleSignup} className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                 <span className="text-red-500">*</span>
              </label>
              <input
                type="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                className="w-full px-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                placeholder=" "
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                 <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                name="fullName"
                value={formData.fullName}
                onChange={handleChange}
                className="w-full px-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                placeholder=" "
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <input
                type="text"
                name="company"
                value={formData.company}
                onChange={handleChange}
                className="w-full px-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                placeholder=" "
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <input
                type="tel"
                name="phone"
                value={formData.phone}
                onChange={handleChange}
                className="w-full px-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                placeholder=" "
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                 <span className="text-red-500">*</span>
              </label>
              <input
                type="password"
                name="password"
                value={formData.password}
                onChange={handleChange}
                className="w-full px-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                placeholder="  ( 6)"
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                  <span className="text-red-500">*</span>
              </label>
              <input
                type="password"
                name="confirmPassword"
                value={formData.confirmPassword}
                onChange={handleChange}
                className="w-full px-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                placeholder="  "
                required
              />
            </div>

            <div className="flex items-start">
              <input
                type="checkbox"
                id="agreeToTerms"
                checked={agreeToTerms}
                onChange={(e) => setAgreeToTerms(e.target.checked)}
                className="mt-1 mr-2"
              />
              <label htmlFor="agreeToTerms" className="text-sm text-gray-700">
                  . <span className="text-red-500">*</span>
                <br />
                <span className="text-xs text-gray-500">
                        , 
                       .
                </span>
              </label>
            </div>

            {error && (
              <div className="text-red-600 text-sm text-center">
                {error}
              </div>
            )}

            <button
              type="submit"
              disabled={isLoading || !agreeToTerms}
              className="w-full bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 transition-colors disabled:bg-gray-400 disabled:cursor-not-allowed"
            >
              {isLoading ? ' ...' : ''}
            </button>

            <div className="text-center mt-4">
              <span className="text-sm text-gray-600">  ? </span>
              <button
                type="button"
                onClick={onBackToLogin}
                className="text-sm text-blue-600 hover:text-blue-800 font-medium"
              >
                
              </button>
            </div>
          </form>
        )}
      </div>
    </div>
  );
};

export default SignupPage;