import React, { useState } from 'react';
import { supabase } from './supabaseClient';

interface LoginPageProps {
  onLoginSuccess: (user: any) => void;
  onShowSignup: () => void;
  onShowForgotPassword: () => void;
}

const LoginPage: React.FC<LoginPageProps> = ({ onLoginSuccess, onShowSignup, onShowForgotPassword }) => {
  const [loginForm, setLoginForm] = useState({
    email: '',
    password: ''
  });
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');


  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setError('');
    
    try {
      //   
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailRegex.test(loginForm.email)) {
        setError('   .');
        setIsLoading(false);
        return;
      }

      // Supabase Auth 
      const { data: authData, error: authError } = await supabase.auth.signInWithPassword({
        email: loginForm.email,
        password: loginForm.password
      });
      
      if (authError) {
        if (authError.message.includes('Invalid login credentials')) {
          setError('    .');
        } else if (authError.message.includes('Email not confirmed')) {
          setError('  .      .');
        } else if (authError.message.includes('Too many requests')) {
          setError('   .    .');
        } else {
          setError(' .   .');
        }
        setIsLoading(false);
        return;
      }

      if (authData?.user) {
        //  
        const user = {
          id: authData.user.id,
          email: authData.user.email,
          fullName: authData.user.user_metadata?.full_name || '',
          role: authData.user.user_metadata?.role || 'user',
          company: authData.user.user_metadata?.company || '',
          phone: authData.user.user_metadata?.phone || ''
        };
        
        onLoginSuccess(user);
      }
      
    } catch (error) {
      console.error(' :', error);
      setError('   .');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 flex items-center justify-center">
      <div className="bg-white p-8 rounded-lg shadow-lg w-full max-w-md">
        <div className="text-center mb-8">
          <div className="flex items-center justify-center gap-2 mb-3">
            <img src="/wideincheon-logo.png" alt="" className="h-8 w-auto" />
            <span className="text-gray-400 text-lg"></span>
            <img src="/youngjin-logo.png" alt="" className="h-8 w-auto" />
          </div>
          <h1 className="text-3xl font-bold text-gray-800">  </h1>
          <p className="text-gray-600 mt-2">  </p>
        </div>
        
        <form onSubmit={handleLogin} className="space-y-6">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              
            </label>
            <input
              type="email"
              value={loginForm.email}
              onChange={(e) => setLoginForm({ ...loginForm, email: e.target.value })}
              className="w-full px-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              placeholder=" "
              required
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              
            </label>
            <input
              type="password"
              value={loginForm.password}
              onChange={(e) => setLoginForm({ ...loginForm, password: e.target.value })}
              className="w-full px-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              placeholder=" "
              required
            />
          </div>
          
          {error && (
            <div className="text-red-600 text-sm text-center">
              {error}
            </div>
          )}
          
          <div className="flex items-center justify-between">
            <button
              type="button"
              onClick={onShowForgotPassword}
              className="text-sm text-blue-600 hover:text-blue-800"
            >
               ?
            </button>
          </div>
          
          <button
            type="submit"
            disabled={isLoading}
            className="w-full bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 transition-colors disabled:bg-gray-400 disabled:cursor-not-allowed"
          >
            {isLoading ? ' ...' : ''}
          </button>
        </form>
        
        <div className="mt-6 text-center">
          <span className="text-sm text-gray-600"> ? </span>
          <button
            onClick={onShowSignup}
            className="text-sm text-blue-600 hover:text-blue-800 font-medium"
          >
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;